import React from "react";
import axios from "axios";
import styled from "styled-components";

import { useState, useEffect } from "react";


const CoverImage = styled.img`
  width: calc(100vw - 48px);
  height: 25vh;

  object-fit: cover;
`;

const FamilyName = styled.div`
  font-family: "Spoqa Han Sans Bold";
  font-size: 16px;
  color: #000000;

  margin-top: 18px;
`;

const Bio = styled.div`
  font-family: "Spoqa Han Sans Medium";
  font-size: 14px;
  color: #7b7b7b;

  margin-top: 6px;
`;
const FieldDom = styled.div`
  width: calc(100vw - 48px);
  height: 20px;

  position: relative;
  margin-top: 32px;
  margin-bottom: 4px;
  display: flex;
`;


const Family = () => {
  const [familyData, setFamilyData] = useState([]);

  useEffect(() => {
    axios("/dummy/FamilyResponse.json")
      .then((res) => res.data)
      .then((data) => {
        setFamilyData((cur) => (cur = data.data));
      });
  }, []);

  return (
    <>
        <FieldDom >

      <Bio>가족 정보</Bio>
        </FieldDom>
      <CoverImage src={familyData.cover_image} />

      <div
        style={{
          width: "calc(100vw - 48px)",
          paddingLeft: "24px",
          borderTop: "1px solid #ABABAB",
        }}
      >
        <FamilyName>{familyData.family_name}</FamilyName>
        <Bio>{familyData.bio}</Bio>
        <div
            style={{
                fontFamily: "Spoqa Han Sans Regular",
                fontSize: "14px",
                color: "#838078",
                position: 'absolute',
                right: "24px",
            }}
        >
            {"가족 정보 수정 >"}
        </div>
      </div>
    </>
  );
};

export default Family;
