import React from "react";
import styled from "styled-components";

const TagDom = styled.span`
  height: 24px;
  font-family: "Spoqa Han Sans Regular";
  font-size: 16px;

  display: inline-block;

  border-radius: 16px;
  background-color: ${(props) => (props.isClicked ? "#6E9972" : "#ecedf1")};
  color: ${(props) => (props.isClicked ? "#FFFFFF" : "#000000")};

  padding: 4px 8px 4px 8px;
  margin: 4px;
`;

const Tag = ({ key, isClicked, state, tag }) => {
  const onClick = () => {
    let newState = state.clicks[0];
    if (newState[key]) newState[key] = false;
    else newState[key] = true;
    state.clicks[1](newState);
  };
  return (
    <>
      <TagDom key={key} isClicked={state.clicks[0][key]} onClick={onClick}>
        #{tag}
      </TagDom>
    </>
  );
};

export default Tag;
