import React from "react";
import styled from "styled-components";
import axios from "axios";

import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

import Gallery from "./Gallery";
import ShowNone from "../ShowNone";

import "../../fonts/fonts.css";

const MobileDom = styled.div`
  width: calc(100% - 48px);
  height: calc(100% - 20px);
`;

const FloatingButton = styled.div`
  font-family: "Spoqa Han Sans Regular";
  font-size: 24px;
  color: ${(props) => (props.isFloating ? "#000000" : "#FFFFFF")};

  width: 48px;
  height: 48px;

  border-radius: 100%;
  background-color: ${(props) => (props.isFloating ? "#FFFFFF" : "#6e9972")};
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);

  position: absolute;
  right: 10px;
  bottom: 64px;

  z-index: 2;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalDom = styled.div`
  width: 100%;
  height: 80vh;

  background-color: rgba(0, 0, 0, 0.5);

  position: absolute;

  z-index: 2;
`;

const Window = styled.div`
  width: 120px;
  height: 100px;

  position: absolute;
  right: 10px;
  bottom: 84px;

  box-shadow: 0 4px 4px 0 rgba(116, 116, 116, 0.5);

  background-color: #fdfcfa;
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  // justify-content: space-evenly;
  align-items: center;
`;

const FloatingMenu = styled.div`
  width: 100px;
  height: 45px;

  font-family: "Spoqa Han Sans Bold";
  font-size: 14px;

  display: flex;
  flex-direction: row;
  align-items: center;

  cursor: pointer;
`;

const FloatingImage = styled.img`
  margin-right: 6px;
`;

const FieldDom = styled.div`
  width: 260px;

  margin-top: 24px;  
`;

const FieldTitle = styled.div`
  font-family: "Spoqa Han Sans Regular";
  font-size: 12px;
  color: #8b94a1;
`;

const FieldText = styled.input`
  width: 260px;
  height: 24px;

  font-family: "Spoqa Han Sans Regular";
  font-size: 16px;

  &::placeholder {
    color: #dedede;
  }

  border: 0px;
  border-bottom: 1px solid #6e9972;

  margin-top: 8px;
`;

const ButtonDom = styled.div`
  width: 100%;
  height: 12.5%;

  display: flex;

  position: absolute;
  bottom: 0;
`;

const Button = styled.div`
  font-family: "Spoqa Han Sans Bold";
  font-size: 2vh;
  color: #ffffff;
  background-color: #6e9972;

  width: 150px;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;
`;

const GalleryList = (props) => {
  const [galleries, setGalleries] = useState([]);
  const [searchedGalleries, setSearchedGalleries] = useState([]);
  const [isFloating, setIsFloating] = useState(false);
  const [isEdit, setIsEdit] = useState(false);

  const [editAlbumId, setEditAlbumId] = useState();
  const [editInput, setEditInput] = useState("");

  const navigate = useNavigate();

  const fetching = () => {
    axios("/dummy/GalleryResponse.json")
      .then((res) => res.data)
      .then((data) => {
        setGalleries((cur) => (cur = data.data));
      });
  };

  const isSearchTagInList = (gallery, searchWord) => {
    let searched = [];

    for (let i = 0; i < gallery.tags.length; i++) {
      if (gallery.tags[i].includes(searchWord)) {
        searched.push(gallery.tags[i]);
      }
    }

    if (searched.length) return true;

    return false;
  };

  const navigateToAlbum = (album_id) => {
    navigate("/archive/" + album_id);
  };

  useEffect(() => {
    fetching();
    setSearchedGalleries((cur) => (cur = galleries));
  }, []);

  useEffect(() => {
    if (props.input[0] !== "") {
      setSearchedGalleries(
        (cur) =>
          (cur = galleries
            .filter((gallery) => gallery.title.includes(props.input[0]))
            .concat(
              galleries.filter((gallery) =>
                isSearchTagInList(gallery, props.input[0])
              )
            ))
      );
    } else {
      setSearchedGalleries((e) => (e = galleries));
    }
  }, [props.input[0], galleries]);

  return (
    <>
      {isFloating ? (
        <ModalDom onClick={() => setIsFloating(false)}>
          <Window>
            <FloatingMenu
              onClick={() => {
                navigate("/archive/create");
              }}
            >
              <FloatingImage src="/img/archive/album_add.svg" alt="create" />
              새앨범
            </FloatingMenu>
            <FloatingMenu
              onClick={() => {
                setIsFloating(false);
                setIsEdit(true);
              }}
            >
              <FloatingImage src="/img/archive/album_add.svg" alt="edit" />
              수정하기
            </FloatingMenu>
          </Window>
        </ModalDom>
      ) : null}
      {isEdit ? (
        <ModalDom
          style={{
            zIndex: "3",
          }}
          onClick={() => setIsFloating(false)}
        >
          <Window
            style={{
              width: "300px",
              height: "300px",
              position: "relative",
              left: "50%",
              top: "50%",
              transform: "translate(-50%, -50%)",
              bottom: "0",
            }}
          >
            <h3>수정하실 앨범을 선택해주세요</h3>
            <FieldDom>
              <FieldTitle>앨범</FieldTitle>
            <select
              onChange={(e) => {
                setEditAlbumId(e.target.value);
              }}
              value={editAlbumId}
              style={{
                width: "240px",
                marginTop: "8px"
              }}
            >
              {galleries.map((item) => (
                <option value={item.album_id} key={item.title}>
                  {item.title}
                </option>
              ))}
            </select>
            </FieldDom>
            <FieldDom>
              <FieldTitle>앨범 이름</FieldTitle>
              <FieldText
                placeholder="수정 하실 앨범 이름을 선택해주세요"
                value={editInput}
                onChange={(e) => setEditInput(e.target.value)}
              />
            </FieldDom>
            
            <ButtonDom>
              <Button
                style={{
                  backgroundColor: "#B7CCB9",
                  borderRadius: "0 0 0 5px",
                }}
                onClick={() => setIsEdit(false)}
              >
                닫기
              </Button>
              <Button
                style={{
                  borderRadius: "0 0 5px 0",
                }}
                onClick={() => {
                  // POST
                  // Edit album title
                  /*
                    BODY
                    {
                      album_id: editAlbumId,
                      title: editInput
                    }
                  */
                  setIsEdit(false);
                }}
              >
                보내기
              </Button>
            </ButtonDom>
          </Window>
        </ModalDom>
      ) : null}
      <MobileDom>
        {searchedGalleries.length ? (
          searchedGalleries.map((gallery, idx) => (
            <Gallery
              key={gallery.album_id}
              isLast={searchedGalleries.length === idx + 1}
              idx={idx}
              data={gallery}
              onClick={() => navigateToAlbum(gallery.album_id)}
            />
          ))
        ) : (
          <ShowNone
            img="/img/archive/fireworks.png"
            span="기억에 남는 사진이 있으신가요?"
            div="우리 가족의 추억을 쌓아보세요"
          />
        )}

        {/* Dummy Tag for margin-bottom of Last element */}
        <FloatingButton
          isFloating={isFloating}
          onClick={() => setIsFloating(!isFloating)}
        >
          {isFloating ? "X" : "+"}
        </FloatingButton>
        <div style={{ width: "1px", height: "1px" }}></div>
      </MobileDom>
    </>
  );
};

export default GalleryList;
