import React from "react";
import styled from "styled-components";

import { useState } from "react";

import Personal from "./setting/Personal";
import Family from "./setting/Family";

const TitleDom = styled.div`
  width: 100%;
  height: 5.5%;

  display: flex;
  align-items: center;

  margin-top: 1.25vh;
  margin-bottom: 1.25vh;
`;

const Title = styled.div`
  font-family: "Spoqa Han Sans Bold";
  padding-left: 24px;

  font-size: 2.5vh;
`;

const MenuDom = styled.div`
  width: 100%;
  height: 5%;

  border-bottom: 1px solid #F2F2F2;

  display: flex;
  flex-direction: row;
  justify-content: center;
`;

const SettingMenu = styled.div`
  font-family: "Spoqa Han Sans Medium";
  font-size: 16px;
  width: 50%;

  color: ${(props) => props.menu ? "#6E9972" : "#000000"};
  border-bottom: 3px solid ${(props) => props.menu ? "#6E9972" : "none" };

  display: flex;
  justify-content: center;
  align-items: center;
`;

const Setting = () => {
  const [menu, setMenu] = useState(true);
  
  return (
    <>
      <TitleDom>
        <Title>설정</Title>
      </TitleDom>
      <MenuDom>
        <SettingMenu
          menu={menu}
          onClick={()=> setMenu(true)}
        >개인</SettingMenu>
        <SettingMenu
          menu={!menu}
          onClick={()=> setMenu(false)}
        >가족</SettingMenu>
      </MenuDom>

      {
        menu ? 
         <Personal />
        :
        <Family />
      }
    </>
  );
};

export default Setting;
