import React from "react";
import styled from "styled-components";
import AWS from "aws-sdk";

import { useState } from "react";
import { useNavigate } from "react-router-dom";

import Tag from "../../components/Archive/Tag";

const CreateDom = styled.div`
  width: 100%;
  height: 100%;
`;

const TitleDom = styled.div`
  width: 100%;
  height: 5.5%;

  display: flex;
  align-items: center;

  margin-top: 1.25vh;
  margin-bottom: 1.25vh;
`;

const Title = styled.div`
  font-family: "Spoqa Han Sans Bold";
  padding-left: 24px;

  font-size: 2.5vh;
`;

const BackButton = styled.img`
  margin-left: 24px;
`;

const CoverImage = styled.div`
  width: 100%;
  height: 22vh;
  background-image: url("https://user-images.githubusercontent.com/12531340/185528537-24edb496-6673-4c2c-bec6-346ab767fa3c.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const FieldFile = styled.input`
  height: 24px;

  font-family: "Spoqa Han Sans Regular";

  border: 0px;

  margin-top: 5vh;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
`;

const FieldDom = styled.div`
  width: calc(100vw - 48px);

  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 32px;
`;

const FieldTitle = styled.div`
  font-family: "Spoqa Han Sans Regular";
  font-size: 12px;
  color: #8b94a1;
`;

const FieldText = styled.input`
  width: 100%;
  height: 24px;

  font-family: "Spoqa Han Sans Regular";
  font-size: 16px;

  &::placeholder {
    color: #dedede;
  }

  border: 0px;
  border-bottom: 1px solid #6e9972;

  margin-top: 8px;
`;

const TagDom = styled.div`
  width: calc(100vw - 48px);
  height: 300px;

  padding-top: 8px;
`;

const ArchiveCreate = () => {
  const [albumInfo, setAlbumInfo] = useState({
    title: "",
    cover_image: "",
    album_image: [],
    tag1: "",
    tag2: "",
    tag3: "",
  });
  const [clicks, setClicks] = useState([false,false,false,false,false,false,false,false]);
  const navigate = useNavigate();
  const tags = [
    "일상",
    "여행",
    "반려동물",
    "힐링",
    "생일",
    "파티",
    "기념일",
    "건강",
  ];

  const getUploadFileURL = async (file, uploadFileName) => {
    const region = "ap-northeast-2";
    const bucket = "osds-bucket";
    const endpoint = "image/";

    AWS.config.update({
      region: region,
      accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY_ID,
      secretAccessKey: process.env.REACT_APP_AWS_SECRET_ACCESS_KEY,
    });

    const upload = new AWS.S3.ManagedUpload({
      params: {
        Bucket: bucket, // 버킷 이름
        Key: endpoint + uploadFileName + ".png", // 유저 아이디
        Body: file, // 파일 객체
      },
    });

    const promise = upload.promise();
    promise.then(
      function (e) {
        alert("파일이 업로드 되었습니다.");

        setAlbumInfo(
          (cur) =>
            (cur = {
              ...albumInfo,
              cover_image: e.Location,
            })
        );
      },
      function (err) {
        alert("파일 업로드에 실패하였습니다.");

        setAlbumInfo(
          (cur) =>
            (cur = {
              ...albumInfo,
              cover_image: "unknown.png",
            })
        );
        return console.log(err);
      }
    );
  };

  return (
    <>
      <CreateDom>
        <TitleDom>
          <BackButton
            src="/img/archive/back_black.svg"
            alt="arrow_back"
            onClick={() => navigate("/archive")}
          />
          <Title>새 앨범 추가하기</Title>
        </TitleDom>
        <FieldDom>
          <FieldTitle>앨범 표지 사진</FieldTitle>
          <CoverImage>
            <FieldFile
              type="file"
              onChange={(e) => {
                getUploadFileURL(
                  e.target.files[0],
                  albumInfo.title + "_cover_image"
                );
              }}
            />
          </CoverImage>
        </FieldDom>
        <FieldDom>
          <FieldTitle>앨범 이름</FieldTitle>
          <FieldText
            placeholder="앨범 이름을 입력하세요"
            value={albumInfo.title}
            onChange={(e) =>
              setAlbumInfo({
                ...albumInfo,
                title: e.target.value,
              })
            }
          />
        </FieldDom>
        <FieldDom>
          <FieldTitle>해시 태그 (최대 3개)</FieldTitle>
          <TagDom>
            {tags.map((tag, idx) => (
              <>
                <Tag
                  key={idx}
                  state={{
                    clicks: [clicks, setClicks]
                  }}
                  tag={tag}
                />
                {idx % 4 === 3 ? <br /> : null}
              </>
            ))}
          </TagDom>
        </FieldDom>
      </CreateDom>
    </>
  );
};

export default ArchiveCreate;
